<template>
  <transition name="slide-fade">
      <div class='alert-pop' v-if="isOpen">
        <div class="status" :class='imgClass'>
        </div>
        <div class="alert-msg">{{options.msg}}</div>
        <i class="el-icon-close" @click='isOpen=false'></i>
      </div>
</transition>
</template>

<script type="text/babel">
const Class_map = {
  'default': 'statu-default',
  'warn': 'statu-warn',
  'success': 'statu-success',
  'error': 'statu-err'
}
  export default {
    data () {
      return {
        isOpen: true,
        delay: this.options.delay || 3000,
        autoClose: this.options.autoClose,
        type: this.options.type || 'default'
      }
    },
    props: {
      options: {}
    },
    computed: {
      imgClass () {
        return Class_map[this.type] || 'statu-default'
      }
    },

    watch: {
    },

    methods: {
      destroyElement () {
        // this.$el.removeEventListener('transitionend', this.destroyElement)
        // this.$destroy(true)
        // this.$el.parentNode.removeChild(this.$el)
      },

      close () {
        // this.closed = true
        // if (typeof this.onClose === 'function') {
        //   this.onClose(this)
        // }
      },

      clearTimer () {
        // clearTimeout(this.timer)
      },

      startTimer () {
        setTimeout(() => { this.isOpen = false }, this.delay)
      }
    },

    mounted () {
      // console.log(this)
      if (this.autoClose || typeof this.autoClose === 'undefined') {
        this.startTimer()
      }
    }
  }
</script>
<style>
.slide-fade-enter-active {
  transition: all .3s ease;
}
.slide-fade-leave-active {
  transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.slide-fade-enter, .slide-fade-leave-to
/* .slide-fade-leave-active for below version 2.1.8 */ {
  transform: translateX(10px);
  opacity: 0;
}
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s
}
.fade-enter, .fade-leave-to /* .fade-leave-active in below version 2.1.8 */ {
  opacity: 0
}
  .alert-pop{
    box-shadow: 0 2px 4px rgba(0,0,0,.12), 0 0 6px rgba(0,0,0,.04);
    min-width: 330px;
    padding: 10px 15px;
    box-sizing: border-box;
    border-radius: 2px;
    position: fixed;
    left: 50%;
    top: 20px;
    -ms-transform: translateX(-50%);
    transform: translateX(-50%);
    background-color: #fff;
    transition: opacity .3s,transform .4s;
    overflow: hidden;
  }
  .alert-msg {
    margin-left: 38px;
    position: relative;
    height: 20px;
    line-height: 20px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
    align-items: center;
  }
  .alert-pop .el-icon-close{
    position: absolute;
    top: 12px;
    right: 10px;
  }
  .status {
    width: 44px;
    height: 44px;
    position: absolute;
    left: 0;
    top: 0;
  }
  .statu-err {
    background: url(data:image/svg+xml;base64,) no-repeat;
  }
  .statu-warn {
    background: url(data:image/svg+xml;base64,) no-repeat;
  }
  .statu-success {
    background: url(data:image/svg+xml;base64,) no-repeat;
  }
  .statu-default {
    background: url(data:image/svg+xml;base64,) no-repeat;
  }
</style>



